:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

body {
  text-align: center;
}

.form-container {
  z-index: 1;
  width: 100%;
  text-align: center;
  opacity: .9;
  background-color: #6a5acd;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
}

.form-container:hover {
  opacity: 1;
}

.search-form {
  justify-content: center;
  display: flex;
}

.search-form input {
  width: 300px;
  padding: 4px 5px;
  font-family: sans-serif;
}

.search-form button {
  cursor: pointer;
}

.gallery {
  max-width: 1416px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 290px;
  justify-content: center;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
  position: relative;
  top: 40px;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-card img:hover {
  transform: scale(1.06);
}

.photo-card img {
  height: 220px;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.info-item {
  text-align: center;
  line-height: 25px;
}

.load-more {
  visibility: hidden;
  position: fixed;
  bottom: 0;
}

a {
  color: #000;
  text-decoration: none;
}

a:link, a:visited {
  color: #000;
}

.info {
  display: flex;
}

.form-btn__icon {
  width: 18px;
  height: 26px;
  fill: #000;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.search-form button:hover .form-btn__icon {
  fill: #6a5acd;
}

